nav {
    min-height: 10vh;
    display: flex;
    justify-content: space-around;
    align-items: center;

    button{
        background: transparent;
        border: 2px solid rgb(65, 65, 65);
        cursor: pointer;
        padding: 0.5rem;
        transition: all 0.3s ease;
        font-weight: 700;

        &:hover {
            background: rgb(65, 65, 65);
            color: #fff;
        }

        &:focus{
            outline: none;
        }

    }
}